import React, { Component } from "react";
import firebase from "firebase";
import { auth } from "./firebase/firebase";

import Navbar from "./Navbar";
import MessageInput from "./MessageInput";

import "../css/Details.css";

var data;
var messages = [];

export default class Details extends Component {
	constructor() {
		super();

		this.state = {
			currentItem: "",
			username: "",
			items: [],
			user: null,
			message: "",
			uid: null,
			response: false,
			messages: null
		};
	}

	componentDidMount() {
		auth.onAuthStateChanged(user => {
			if (user) {
				this.setState({
					user: user,
					uid: user.uid
				});
			} else {
				console.log("OH NO! NOT SIGNED IN lol");
			}
		});

		data = this.props.location.state;

		this.getMessages();
		setInterval(() => {
			this.getMessages();
		}, 1);
	}

	getMessages = () => {
		let messageData = [];
		if (this.state.uid === data.sellerUid) {
			// Have them select which convo to look at, impliment later
		} else {
			firebase
				.database()
				.ref("/messages/" + data.listingKey)
				.once("value")
				.then(snapshot => {
					snapshot.forEach(childsnapshot => {
						var sender =
							childsnapshot.val().from === this.state.uid;
						messageData.push([childsnapshot.val().message, sender]);
					});
				})
				.then(() => {
					this.setState({
						messages: messageData,
						response: true
					});
				});
		}
	};

	render() {
		if (this.state.response) {
			return (
				<div>
					<Navbar />
					<div id="details-container">
						<h1>{data.title}</h1>
						<p>{data.grade}</p>
						<h5>{data.description}</h5>

						<div id="messageDiv">
							{this.state.messages.map((message, i) => {
								return (
									<div key={i} className="messageDiv">
										<p
											key={i}
											className={message[1].toString()}
										>
											{message[0]}
										</p>
										<br /> <br />
									</div>
								);
							})}
						</div>

						<MessageInput
							sellerUid={data.sellerUid}
							listing={data.listingKey}
						/>
					</div>
				</div>
			);
		} else {
			return <h1>Loading...</h1>;
		}
	}
}
