import React, { Component } from "react";
import firebase from "./firebase/firebase";

import Navbar from "./Navbar";
import Card from "./Card";

import "../css/Listings.css";

var data = undefined;
var numChildren = undefined;
var count = 0;
var listings = [];

export default class Listings extends Component {
	constructor() {
		super();

		this.state = {
			response: false
		};
	}

	getListings = grade => {
		if (grade !== "Diploma Program") {
			firebase
				.database()
				.ref("/listings/grade" + grade)
				.once("value")
				.then((snapshot, i) => {
					numChildren = snapshot.numChildren();
					if (numChildren === 0) {
						this.setState({
							response: true
						});
					}
					snapshot.forEach(childsnapshot => {
						const title = childsnapshot.val().title;
						const grade = childsnapshot.val().grade;
						const desc = childsnapshot.val().description;
						const email = childsnapshot.val().email;
						const uid = childsnapshot.val().uid;
						const listingKey = Object.keys(snapshot.val())[count];

						const listing = [
							title,
							grade,
							desc,
							email,
							uid,
							listingKey
						];
						console.log(listing);
						listings.push(listing);
						count++;
						// console.log("HI@");
						if (count === numChildren) {
							// console.log(listings);
							this.setState({
								response: true
							});
						}
					});
				});
		} else {
			alert("Can't get listings for DP yet.");
			// firebase
			//     .database()
			//     .ref("/listings/diploma")
			//     .once("value")
			//     .then(snapshot => {
			//         snapshot.forEach(childsnapshot => {
			//             console.log(childsnapshot.val());
			//             const title = childsnapshot.val().title;
			//             const grade = childsnapshot.val().grade;
			//             const desc = childsnapshot.val().description;
			//             const email = childsnapshot.val().email;
			//             const uid = childsnapshot.val().uid;
			//             const listing = [title, grade, desc, email, uid];
			//             listings.push(listing);
			//         });
			//     });
		}
	};

	componentDidMount() {
		data = this.props.location.state;

		this.getListings(data.grade);
		// console.log(data, listingKey);

		// console.log(listings);
	}

	render() {
		if (this.state.response) {
			return (
				<div>
					<Navbar />
					{data.grade === "Diploma Program" ? (
						<div>
							<h1>Diploma Program Listings</h1>
						</div>
					) : (
						<div>
							<h1 id="listingsTitle">
								Grade {data.grade} Listings
							</h1>

							<div id="listingCardContainer">
								{listings.map((data, i) => {
									return (
										<Card
											key={i}
											title={data[0]}
											grade={data[1]}
											description={data[2]}
											uid={data[4]}
											listingKey={data[5]}
										/>
									);
								})}
							</div>
						</div>
					)}
				</div>
			);
		} else {
			return <h1>Loading...</h1>;
		}
	}
}
